.program-bike,
.program-logo {
  //Permet de centrer les images
  margin: 0 auto;
  text-align: center;
}
.program-logo {
  width: 70%;
  max-height: 350px;
}

.program-bike {
  width: 80%;
  max-width: 80%;
  height: 40vh; /* Hauteur fixe */
  display: flex;
  align-items: center;
  justify-content: center;

  > svg {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
    object-fit: contain; /* Maintient les proportions */
  }
}
